import 'whatwg-fetch';

const fetchReflections =
window.fetch('https://sheets.googleapis.com/v4/spreadsheets/1XkJ4QD8pzoOwL97lFtTSAv2fOb3DikNQYDNkXn6LL9Y/values/Reflections?key=AIzaSyBXC3NZmF3G0LK50YSS4EY4yxb3W7AJa80')
    .then(r => r.json())
    .then(res => {
        let reflections = {};
        for(let i = 1; i < res.values.length; i++) {
            const skills_a = res.values[i][9]?.split('\n');
            const skills_b = res.values[i][10]?.split('\n')
            reflections[res.values[i][1]] = {
                // name: res.values[i][0],
                // suitName: res.values[i][1],
                tier: res.values[i][2],
                images: {
                    icon: res.values[i][3],
                    original: res.values[i][4],
                    awakened: res.values[i][5]
                },
                CoR: [
                    null,
                    res.values[i][7]?.replace('A%', `<b style="color:#d1668f">${skills_a[0]}</b>`)?.replace('B%', `<b style="color:#d1668f">${skills_b[0]}</b>`)?.replace('\n\n', ' · '),
                    res.values[i][7]?.replace('A%', `<b style="color:#d1668f">${skills_a[1]}</b>`)?.replace('B%', `<b style="color:#d1668f">${skills_b[1]}</b>`)?.replace('\n\n', ' · '),
                    res.values[i][7]?.replace('A%', `<b style="color:#d1668f">${skills_a[2]}</b>`)?.replace('B%', `<b style="color:#d1668f">${skills_b[2]}</b>`)?.replace('\n\n', ' · '),
                    res.values[i][7]?.replace('A%', `<b style="color:#d1668f">${skills_a[3]}</b>`)?.replace('B%', `<b style="color:#d1668f">${skills_b[3]}</b>`)?.replace('\n\n', ' · '),
                    res.values[i][7]?.replace('A%', `<b style="color:#d1668f">${skills_a[4]}</b>`)?.replace('B%', `<b style="color:#d1668f">${skills_b[4]}</b>`)?.replace('\n\n', ' · ')
                ]
            };
        }
        return reflections;
    })

const fetchFavouriteCounts = window.fetch('https://sn-suit-reference-api.herokuapp.com/favourites')
        .then(r => r.json())
        .then(res => {
            let counts = {};
            for(let i = 1; i < res.length; i++) {
                counts[res[i].suitid] = parseInt(res[i].num)
            }
            return counts;
        })

function formatEventDates(start, end) {
    if(!start && !end) return null;
    return {
        start,
        end
    }
}

function formatEventDateList(start1, end1, start2, end2) {
    return [
        formatEventDates(start1, end1),
        formatEventDates(start2, end2),
    ].filter(obj => obj !== null);
}

const fetchEvents = window.fetch('https://sheets.googleapis.com/v4/spreadsheets/1XkJ4QD8pzoOwL97lFtTSAv2fOb3DikNQYDNkXn6LL9Y/values/Events?key=AIzaSyBXC3NZmF3G0LK50YSS4EY4yxb3W7AJa80')
        .then(r => r.json())
        .then(res => {
            let events = {};
            for(let i = 1; i < res.values.length; i++) {
                events[res.values[i][0]] = {
                    name: res.values[i][0],
                    nickname: res.values[i][1],
                    type: res.values[i][2],
                    releases: {
                        TW: formatEventDateList(
                            res.values[i][3],
                            res.values[i][4],
                            res.values[i][5],
                            res.values[i][6],
                        ),
                        JP: formatEventDateList(
                            res.values[i][7],
                            res.values[i][8],
                            res.values[i][9],
                            res.values[i][10],
                        ),
                        EN: formatEventDateList(
                            res.values[i][11],
                            res.values[i][12],
                            res.values[i][13],
                            res.values[i][14],
                        ),
                    },
                    suits: []
                }
            }
            return events;
        })

export default function fetchAllSuits() {
    return Promise.all([
        fetchReflections,
        fetchFavouriteCounts,
        fetchEvents
    ])
    .then((values) => {
        const [reflections, favouriteCounts, eventInfo] = values;
        return window.fetch('https://sheets.googleapis.com/v4/spreadsheets/1XkJ4QD8pzoOwL97lFtTSAv2fOb3DikNQYDNkXn6LL9Y/values/Suits?key=AIzaSyBXC3NZmF3G0LK50YSS4EY4yxb3W7AJa80')
        .then(r => r.json())
        .then(res => {
            let suits = [];
            for(let i = 1; i < res.values.length; i++) {
                let suitid = res.values[i][0].replace(/ /g, '-').toLowerCase();
                suits.push({
                    name: res.values[i][0],
                    id: res.values[i][0].replace(/ /g, '-').toLowerCase(),
                    awakenedName: res.values[i][1],
                    reflection: {
                        name: res.values[i][5],
                        ...reflections[res.values[i][0]]
                    },
                    detailPageUrl: `https://asukii314.github.io/sn-suit-reference/#/${suitid}`,
                    likes: favouriteCounts[suitid] || 0,
                    designer: res.values[i][5],
                    rarity: res.values[i][2],
                    attribute: res.values[i][3],
                    nation: res.values[i][4],
                    archive: res.values[i][6],
                    images: {
                        promo: res.values[i][11],
                        detail: res.values[i][12],
                        original: res.values[i][13],
                        awakened: res.values[i][14],
                        'idea lab': res.values[i][15],

                    },
                    video: res.values[i][16],
                    source: {
                        type: res.values[i][7].split(' - ')[0],
                        subtype: res.values[i][7].split(' - ')[1],
                        eventName: res.values[i][8],
                        event: {
                            name: res.values[i][8],
                            ...eventInfo[res.values[i][8]]
                        }
                    },
                    availability: {
                        TW: true,
                        JP: res.values[i][9] === 'TRUE',
                        EN: res.values[i][10] === 'TRUE'
                    },
                });
            }

            for(let i = 1; i < suits.length; i++) {
                if(suits[i].source.eventName) {
                    suits[i].source.event.suits =
                    suits.filter(suit => suit.source.eventName === suits[i].source.eventName)
                    .map(suit => {
                        const suitid = suit.name.replace(/ /g, '-').toLowerCase();
                        return {
                            name: suit.name,
                            rarity: suit.rarity,
                            type: (suit.source.subtype === 'event' ? 'pavillion' : suit.source.subtype || 'other'),
                            detailPageUrl: `https://asukii314.github.io/sn-suit-reference/#/${suitid}`
                        }
                    })
                }
            }
            return suits;
        })
    })
}

// maybe figure out if the sheets api has a less hacky way to do this, but ehhhh
export function fetchSuitByName(suitName) {
    return fetchAllSuits().then(
        suits => suits.filter(suit => suit.name.toLowerCase() === suitName.toLowerCase())?.[0]
    )
}
